import React,{useState} from 'react';
import Heading from './header';
 import Note from './note';
import Footer from './footer';
 import Keep from './createnote';
 


function App(){
const[notes,setNotes]=useState([]);

function addNote(newNote) {
    setNotes(prevNote =>{
        return [...prevNote,newNote];
    });
}
function deleteNote(id){
setNotes(prevNote =>{
    return prevNote.filter((noteTerm,index)=>{
  return index !== id;
    })
})
}
    return(
    <div>
    <Heading />
    <Keep 
     onAdd={addNote}
    />
     {notes.map((noteTerm,index)=> {
        return <Note key={index} id={index} 
        title={noteTerm.title} 
        content={noteTerm.content}
        onDelete={deleteNote} />
        } )} 

    <Footer /> 
      
    </div>
    );
     }

export default App;